import React, { useEffect, useState, useLayoutEffect, Fragment } from "react";
import io from "socket.io-client";
// redux components
import { connect } from "react-redux";
import {
  autoLogoutRequest,
  userLoadRequest,
  resetLoadUser,
} from "../../Redux/Actions/UserActions/userActions";
import { currenciesUpdateRequest } from "../../Redux/Actions/MarketDataActions/Currencies/getCurrenciesActions";

// core components
import Button from "../Core/Button.js";
import GridItem from "../Core/GridItem.js";
import GridContainer from "../Core/GridContainer.js";
import Table from "../Core/Table.js";
import Card from "../Core/Card.js";
import CardHeader from "../Core/CardHeader.js";
import CardBody from "../Core/CardBody.js";
import CardAvatar from "../Core/CardAvatar.js";
import profile from "../../assets/img/profile.jpg";
// styling components
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import styles from "../Styles/userPageStyle.js";
import Lottie from "react-lottie";
import * as trailLoading from "../../assets/gifs/trailLoading.json";
import * as flags from "../../../node_modules/react-round-flags/flags";
import world from "../../assets/img/world1.jpg";
// @material-ui/core
import { withStyles, makeStyles } from "@material-ui/core";

const flag = ({ size, code, style }) => {
  const flag = flags[`icons${size}`][code];
  if (flag) {
    return (
      <img
        src={flag}
        style={({ width: size, height: size }, style)}
        alt="..."
      />
    );
  } else {
    return (
      <img
        width="48px"
        height="36px"
        src={world}
        maxWidth="64px"
        maxHeight="64px"
        alt="..."
      />
    );
  }
};

const useStyles = makeStyles(styles);
const defaultOptions = {
  loop: true,
  autoplay: true,
  animationData: trailLoading.default,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
  },
};

//http://localhost:3000/socket.io
//https://www.moneyexchangeco.com/socket.io

function Currencies(props) {
  const {
    user,
    lex,
    session,
    autoDisconnect,
    currencies,
    loadedUser,
    loadedCurrencies,
    autoLogoutRequest,
    userLoadRequest,
    currenciesUpdateRequest,
    resetLoadUser,
  } = props;
  const [time, setTime] = useState("");
  const [date, setDate] = useState("");
  const classes = useStyles();

  useLayoutEffect(() => {
    if (autoDisconnect) {
      props.autoLogoutRequest(lex);
      props.history.push("/loginPage");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [autoDisconnect]);

  useEffect(() => {
    if (
      document.cookie.match("connect.sid") &&
      document.cookie.match("connect.sid").input === session
    ) {
      userLoadRequest();

      const socket = io("https://www.moneyexchangeco.com/socket.io");
      socket.on("message", ({ currencies, serverDate, serverTime }) => {
        setTime(serverTime);
        setDate(serverDate);
        currenciesUpdateRequest(currencies);
      });
      return () => {
        socket.close();
        resetLoadUser();
      };
    } else {
      autoLogoutRequest(lex);
      props.history.push("/loginPage");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const renderHeadLines = (name, type) => {
    if (type === "category") {
      return (
        <h6 className={classes.cardCategory} color="primary">
          {name}
        </h6>
      );
    } else {
      return (
        <h6 className={classes.cardTitle} color="primary">
          {name}
        </h6>
      );
    }
  };

  const renderRatesValues = (value) => {
    return (
      <h5 className={classes.cardTitle} color="primary">
        {`${value.toFixed(6).slice(0, -4)}`}
      </h5>
    );
  };

  return (
    <Fragment>
      <ToastContainer />
      {loadedCurrencies && loadedUser ? (
        <Fragment>
          <GridContainer>
            <GridItem xs={12} sm={12} md={12}>
              {user.userLanguage === "english" ? (
                <Button fullWidth color="info" round>
                  {time
                    ? "last update : " + date + " " + time
                    : " updating ..."}
                </Button>
              ) : (
                <Button fullWidth color="info" round>
                  {time ? "  : " + date + " " + time : "... "}
                </Button>
              )}
            </GridItem>
          </GridContainer>
          <GridContainer>
            <GridItem xs={12} sm={12} md={9}>
              <Card>
                <CardHeader color="primary">
                  {user.userLanguage === "english" ? (
                    <h4 className={classes.cardTitleWhite}>Currencies Table</h4>
                  ) : (
                    <h4
                      className={classes.cardTitleWhite}
                      style={{
                        textAlign: "right",
                      }}
                    >
                       
                    </h4>
                  )}
                </CardHeader>
                <CardBody>
                  {user.userLanguage === "english" ? (
                    <Table
                      tableHeaderColor="primary"
                      tableHead={[
                        renderHeadLines("", "category"),
                        renderHeadLines("currency", "category"),
                        renderHeadLines("value", "category"),
                        renderHeadLines("buy cash", "category"),
                        renderHeadLines("sell cash", "category"),
                        renderHeadLines("buy transfer", "category"),
                        renderHeadLines("sell transfer", "category"),
                      ]}
                      tableData={user.currenciesRates.map((rate) =>
                        rate.isInTable
                          ? [
                              <CardAvatar>
                                {flag({
                                  size: 48,
                                  code: `${rate.currencyName
                                    .substring(0, 2)
                                    .toUpperCase()}`,
                                })}
                              </CardAvatar>,
                              <h6
                                className={classes.cardTitle}
                                align="left"
                                color="primary"
                              >
                                {rate.currencyName}
                              </h6>,
                              renderRatesValues(currencies[rate.currencyName]),
                              renderRatesValues(
                                rate.buyCashRate + currencies[rate.currencyName]
                              ),
                              renderRatesValues(
                                rate.sellCashRate +
                                  currencies[rate.currencyName]
                              ),
                              renderRatesValues(
                                rate.buyTransferRate +
                                  currencies[rate.currencyName]
                              ),
                              renderRatesValues(
                                rate.sellTransferRate +
                                  currencies[rate.currencyName]
                              ),
                            ]
                          : undefined
                      )}
                    />
                  ) : (
                    <Table
                      tableHeaderColor="primary"
                      tableHead={[
                        renderHeadLines(" ", "category"),
                        renderHeadLines(" ", "category"),
                        renderHeadLines(" ", "category"),
                        renderHeadLines(" ", "category"),
                        renderHeadLines("", "category"),
                        renderHeadLines("", "category"),
                        renderHeadLines("", "category"),
                      ]}
                      tableData={user.currenciesRates.map((rate) =>
                        rate.isInTable
                          ? [
                              renderRatesValues(
                                rate.sellTransferRate +
                                  currencies[rate.currencyName]
                              ),
                              renderRatesValues(
                                rate.buyTransferRate +
                                  currencies[rate.currencyName]
                              ),
                              renderRatesValues(
                                rate.sellCashRate +
                                  currencies[rate.currencyName]
                              ),
                              renderRatesValues(
                                rate.buyCashRate + currencies[rate.currencyName]
                              ),
                              renderRatesValues(currencies[rate.currencyName]),
                              <h6
                                className={classes.cardTitle}
                                align="right"
                                color="primary"
                              >
                                {rate.currencyName}
                              </h6>,
                              <CardAvatar>
                                {flag({
                                  size: 48,
                                  code: `${rate.currencyName
                                    .substring(0, 2)
                                    .toUpperCase()}`,
                                })}
                              </CardAvatar>,
                            ]
                          : undefined
                      )}
                    />
                  )}
                </CardBody>
              </Card>
            </GridItem>
            <GridItem xs={12} sm={12} md={3}>
              <Card profile>
                <CardAvatar profile>
                  <img src={profile} alt="..." />
                </CardAvatar>
                <CardBody profile>
                  {renderHeadLines(
                    user.userLanguage === "english" ? "OWNER" : "",
                    "category"
                  )}
                  {renderHeadLines(user.name, "title")}
                  {renderHeadLines(
                    user.userLanguage === "english" ? "email" : "",
                    "category"
                  )}
                  {renderHeadLines(user.email, "title")}
                  {renderHeadLines(
                    user.userLanguage === "english" ? "phone" : "",
                    "category"
                  )}
                  {renderHeadLines(user.phone, "title")}
                  {renderHeadLines(
                    user.userLanguage === "english" ? "address" : "",
                    "category"
                  )}
                  {renderHeadLines(user.address, "title")}
                  {renderHeadLines(
                    user.userLanguage === "english" ? "store name" : " ",
                    "category"
                  )}
                  {renderHeadLines(user.storeName, "title")}
                  {renderHeadLines(
                    user.userLanguage === "english"
                      ? "licence number"
                      : " ",
                    "category"
                  )}
                  {renderHeadLines(user.licenceNumber, "title")}
                </CardBody>
              </Card>
            </GridItem>
          </GridContainer>
        </Fragment>
      ) : (
        <Fragment>
          <Lottie options={defaultOptions} height={200} width={200}>
            LOADING
          </Lottie>
          <h6
            className={classes.cardCategory}
            style={{
              marginLeft: "350px",
            }}
          >
            initializing data, please wait up until 1 minute
          </h6>
        </Fragment>
      )}
    </Fragment>
  );
}

const mapDispatchToProps = (dispatch) => ({
  autoLogoutRequest: (email) => dispatch(autoLogoutRequest(email)),
  userLoadRequest: () => dispatch(userLoadRequest()),
  currenciesUpdateRequest: (currencies) =>
    dispatch(currenciesUpdateRequest(currencies)),
  resetLoadUser: () => dispatch(resetLoadUser()),
});

const mapStateToProps = (state) => {
  return {
    session: state.userReducer.session,
    lex: state.userReducer.lex,
    loadedUser: state.userReducer.loadedUser,
    autoDisconnect: state.userReducer.autoDisconnect,
    user: state.userReducer.user,
    currencies: state.getCurrenciesReducer.currencies,
    loadedCurrencies: state.getCurrenciesReducer.loadedCurrencies,
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(Currencies));
