import React, { useEffect, useLayoutEffect, useState, Fragment } from "react";
//redux
import { connect } from "react-redux";
import {
  autoLogoutRequest,
  userLoadRequest,
  resetLoadUser,
} from "../../Redux/Actions/UserActions/userActions";
import { getCurrenciesRequest } from "../../Redux/Actions/MarketDataActions/Currencies/getCurrenciesActions";
// @material-ui/core
import {
  makeStyles,
  withStyles,
  Divider,
  TextField,
  Paper,
  Menu,
  MenuItem,
} from "@material-ui/core";
// core components
import GridItem from "../Core/GridItem.js";
import GridContainer from "../Core/GridContainer.js";
import Card from "../Core/Card.js";
import CardHeader from "../Core/CardHeader.js";
import CardBody from "../Core/CardBody.js";
import CardAvatar from "../Core/CardAvatar.js";
import Button from "../Core/Button.js";
import calculator from "../../assets/img/calculator.jpg";
// styles
import dropdownStyle from "../Styles/dropdownStyle.js";
import userStyles from "../Styles/userPageStyle.js";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import PropTypes from "prop-types";
import * as flags from "../../../node_modules/react-round-flags/flags";
import Lottie from "react-lottie";
import * as trailLoading from "../../assets/gifs/trailLoading.json";
import world5 from "../../assets/img/world5.jpg";
import world from "../../assets/img/world1.jpg";

const flag = ({ size, code, style }) => {
  const flag = flags[`icons${size}`][code];
  if (flag) {
    return (
      <img
        src={flag}
        style={({ width: size, height: size }, style)}
        alt="..."
      />
    );
  } else if (size === 24) {
    return <img width="24px" height="20px" src={world} alt="..." />;
  } else {
    return (
      <img
        src={world5}
        style={({ width: size, height: size }, style)}
        alt="..."
      />
    );
  }
};

const defaultOptions = {
  loop: true,
  autoplay: true,
  animationData: trailLoading.default,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
  },
};

const styles = (theme) => ({
  ...dropdownStyle(theme),
  ...userStyles,
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0",
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF",
    },
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1",
    },
  },
});

const useStyles = makeStyles(styles);

function Calculator(props) {
  const {
    user,
    currencies,
    autoDisconnect,
    loadedCurrencies,
    lex,
    session,
    loadedUser,
  } = props;
  const classes = useStyles();

  const [currency, setCurrnecy] = useState({
    currencyName: "",
    value: 0.0,
    isInTable: false,
  });

  const [currenciesNames, setCurrencies] = useState({});
  const [rateValue, setRateValue] = useState(0.0);
  const [amount, setAmount] = useState(0.0);
  const [rateInput, setRateInput] = useState(false);
  const [chooseRate, setChooseRate] = useState(false);
  const [openProfile, setOpenProfile] = useState(null);

  useLayoutEffect(() => {
    if (autoDisconnect) {
      props.autoLogoutRequest(lex);
      props.history.push("/loginPage");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [autoDisconnect]);

  useEffect(() => {
    if (
      document.cookie.match("connect.sid") &&
      document.cookie.match("connect.sid").input === session
    ) {
      props.getCurrenciesRequest();
      props.userLoadRequest();
      return () => {
        var id = window.setTimeout(null, 0);
        while (id--) {
          window.clearTimeout(id);
        }

        props.resetLoadUser();
      };
    } else {
      props.autoLogoutRequest(lex);
      props.history.push("/loginPage");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleClickProfile = (event) => {
    if (openProfile && openProfile.contains(event.target)) {
      setOpenProfile(null);
    } else {
      setCurrencies({ ...currencies });
      setOpenProfile(event.currentTarget);
    }
  };

  const handleCloseProfile = () => {
    setOpenProfile(null);
  };

  const handleCurrenciesList = (cName, cValue, cIfInTable) => {
    handleCloseProfile();
    handleCurrencyChoise(cName, cValue, cIfInTable);
    return;
  };

  const handleCurrencyChoise = (currencyName, value, checkIfInTable) => {
    let isInTable = false;
    if (!checkIfInTable) {
      user.currenciesRates.forEach((rate) => {
        if (rate.currencyName === currencyName) {
          isInTable = true;
        }
      });
    } else {
      isInTable = true;
    }
    setAmount(0.0);
    setCurrnecy({ currencyName, value, isInTable });
  };

  const handleRateInput = () => {
    setRateValue(0.0);
    setRateInput(!rateInput);
    setChooseRate(true);
    return;
  };

  const handleNoRateInput = () => {
    setRateValue(0.0);
    setChooseRate(true);
    return;
  };

  const handleRateChoise = (rateAction) => {
    let rateValue;
    user.currenciesRates.forEach((rate) => {
      if (rate.currencyName === currency.currencyName) {
        rateValue = rate[rateAction];
        setRateValue(rateValue);
      }
    });
    setChooseRate(true);
    return;
  };

  return (
    <Fragment>
      <ToastContainer />
      {loadedCurrencies && loadedUser ? (
        <Card>
          {user.userLanguage === "english" ? (
            <Fragment>
              <CardHeader color="primary">
                <h4 className={classes.cardTitleWhite}>Calculator</h4>
              </CardHeader>
              <CardBody>
                <GridContainer>
                  <GridItem xs={12} sm={12} md={3}>
                    <br />
                    <br />
                    {user.currenciesRates.map(
                      (rate) =>
                        rate.isInTable && (
                          <Button
                            fullWidth
                            color="success"
                            key={rate._id}
                            onClick={() =>
                              handleCurrencyChoise(
                                rate.currencyName,
                                currencies[rate.currencyName],
                                true
                              )
                            }
                          >
                            {rate.currencyName}
                          </Button>
                        )
                    )}
                    <h1>{""}</h1>
                    <br />
                    <Divider />
                    <Button
                      fullWidth
                      color="success"
                      aria-controls="long-menu"
                      aria-haspopup="true"
                      onClick={handleClickProfile}
                    >
                      all currencies
                    </Button>
                    <Paper>
                      <Menu
                        id="long-menu"
                        anchorEl={openProfile}
                        open={Boolean(openProfile)}
                        onClose={handleCloseProfile}
                        PaperProps={{
                          style: {
                            maxHeight: 300,
                            width: "35ch",
                          },
                        }}
                      >
                        {Object.keys(currenciesNames).map((curr, i) => (
                          <MenuItem
                            key={i}
                            className={classes.dropdownItem}
                            onClick={() =>
                              handleCurrenciesList(
                                curr,
                                currencies[curr],
                                false
                              )
                            }
                          >
                            <GridContainer>
                              <GridItem>
                                {flag({
                                  size: 24,
                                  code: `${curr.substring(0, 2).toUpperCase()}`,
                                })}
                              </GridItem>
                              <GridItem>
                                <p
                                  className={classes.cardCategory}
                                  style={{
                                    color: "black",
                                    paddingTop: "2px",
                                  }}
                                >
                                  {curr}
                                </p>
                              </GridItem>
                            </GridContainer>
                          </MenuItem>
                        ))}
                      </Menu>
                    </Paper>
                  </GridItem>
                  <GridItem xs={12} sm={12} md={5}>
                    <br />
                    <Card profile>
                      <CardAvatar profile>
                        {flag({
                          size: 64,
                          code: `${currency.currencyName
                            .substring(0, 2)
                            .toUpperCase()}`,
                        })}
                      </CardAvatar>
                      <CardBody profile>
                        {currency.currencyName === "" ? (
                          <Fragment>
                            <br />
                            <h4 className={classes.cardCategory}>
                              please choose currency
                            </h4>
                            <br />
                            <br />
                            <br />
                            <br />
                            <br />
                            <br />
                            <br />
                          </Fragment>
                        ) : (
                          <GridContainer>
                            <GridItem xs={12} sm={12} md={6}>
                              <h6 className={classes.cardCategory}>Currency</h6>
                              <h4 className={classes.cardTitle}>
                                {currency.currencyName}
                              </h4>
                              <br />
                              <h6 className={classes.cardCategory}>Value</h6>
                              <h4 className={classes.cardTitle}>
                                {currency.value.toFixed(6).slice(0, -4) +
                                  " \u20AA"}
                              </h4>
                            </GridItem>
                            <GridItem xs={12} sm={12} md={6}>
                              <Button
                                onClick={() => handleNoRateInput()}
                                fullWidth
                                color="primary"
                                round
                              >
                                no rate
                              </Button>
                              <Button
                                onClick={() => handleRateInput()}
                                fullWidth
                                color="primary"
                                round
                              >
                                manual rate
                              </Button>
                              {rateInput && (
                                <TextField
                                  type="number"
                                  autoFocus
                                  label="manual rate"
                                  value={rateValue}
                                  onChange={(e) => setRateValue(e.target.value)}
                                  fullWidth
                                />
                              )}
                              {currency.isInTable && (
                                <Fragment>
                                  <Button
                                    onClick={() =>
                                      handleRateChoise("buyCashRate")
                                    }
                                    fullWidth
                                    color="primary"
                                    round
                                  >
                                    buy cash
                                  </Button>
                                  <Button
                                    onClick={() =>
                                      handleRateChoise("sellCashRate")
                                    }
                                    fullWidth
                                    color="primary"
                                    round
                                  >
                                    sell cash
                                  </Button>
                                  <Button
                                    onClick={() =>
                                      handleRateChoise("buyTransferRate")
                                    }
                                    fullWidth
                                    color="primary"
                                    round
                                  >
                                    buy transfer
                                  </Button>
                                  <Button
                                    onClick={() =>
                                      handleRateChoise("sellTransferRate")
                                    }
                                    fullWidth
                                    color="primary"
                                    round
                                  >
                                    sell transfer
                                  </Button>
                                </Fragment>
                              )}
                            </GridItem>
                          </GridContainer>
                        )}
                      </CardBody>
                    </Card>
                  </GridItem>
                  <GridItem xs={12} sm={12} md={4}>
                    <br />
                    <Card profile>
                      <CardAvatar profile>
                        <img src={calculator} alt="..." />
                      </CardAvatar>
                      <CardBody profile>
                        {!chooseRate ? (
                          <Fragment>
                            <br />
                            <h4 className={classes.cardCategory}>
                              please choose rate
                            </h4>
                            <br />
                            <br />
                            <br />
                            <br />
                            <br />
                            <br />
                            <br />
                          </Fragment>
                        ) : (
                          <Fragment>
                            <h6 className={classes.cardCategory}>Rate</h6>
                            <h4 className={classes.cardTitle}>{rateValue}</h4>
                            <h6 className={classes.cardCategory}>
                              calculated currency
                            </h6>
                            <h4 className={classes.cardTitle}>
                              {Number(
                                Number(currency.value) + Number(rateValue)
                              )
                                .toFixed(6)
                                .slice(0, -4) + " \u20AA"}
                            </h4>
                            <br />
                            <TextField
                              type="number"
                              label="amount"
                              value={amount}
                              onChange={(e) => setAmount(e.target.value)}
                              fullWidth
                            />
                            <br />
                            <Card plain>
                              <CardHeader plain color="warning">
                                <h4 className={classes.cardTitleWhite}>
                                  total :
                                  {" " +
                                    Number(
                                      (Number(currency.value) +
                                        Number(rateValue)) *
                                        Number(amount)
                                    )
                                      .toFixed(6)
                                      .slice(0, -4) +
                                    " \u20AA"}
                                </h4>
                              </CardHeader>
                            </Card>
                          </Fragment>
                        )}
                      </CardBody>
                    </Card>
                  </GridItem>
                </GridContainer>
              </CardBody>
            </Fragment>
          ) : (
            <Fragment>
              <CardHeader color="primary">
                <h4
                  className={classes.cardTitleWhite}
                  style={{
                    textAlign: "right",
                  }}
                >
                  
                </h4>
              </CardHeader>
              <CardBody>
                <GridContainer>
                  <GridItem xs={12} sm={12} md={4}>
                    <br />
                    <Card profile>
                      <CardAvatar profile>
                        <img src={calculator} alt="..." />
                      </CardAvatar>
                      <CardBody profile>
                        {!chooseRate ? (
                          <Fragment>
                            <br />
                            <h4 className={classes.cardCategory}> </h4>
                            <br />
                            <br />
                            <br />
                            <br />
                            <br />
                            <br />
                            <br />
                          </Fragment>
                        ) : (
                          <Fragment>
                            <h6 className={classes.cardCategory}></h6>
                            <h4 className={classes.cardTitle}>{rateValue}</h4>
                            <h6 className={classes.cardCategory}> </h6>
                            <h4 className={classes.cardTitle}>
                              {" \u20AA" +
                                Number(
                                  Number(currency.value) + Number(rateValue)
                                )
                                  .toFixed(6)
                                  .slice(0, -4)}
                            </h4>
                            <br />
                            <TextField
                              type="number"
                              label=" "
                              value={amount}
                              onChange={(e) => setAmount(e.target.value)}
                              fullWidth
                            />
                            <br />
                            <Card plain>
                              <CardHeader plain color="warning">
                                <h4 className={classes.cardTitleWhite}>
                                  "
                                  {"  :  \u20AA" +
                                    Number(
                                      (Number(currency.value) +
                                        Number(rateValue)) *
                                        Number(amount)
                                    )
                                      .toFixed(6)
                                      .slice(0, -4) +
                                    ""}
                                </h4>
                              </CardHeader>
                            </Card>
                          </Fragment>
                        )}
                      </CardBody>
                    </Card>
                  </GridItem>

                  <GridItem xs={12} sm={12} md={5}>
                    <br />
                    <Card profile>
                      <CardAvatar profile>
                        {flag({
                          size: 64,
                          code: `${currency.currencyName
                            .substring(0, 2)
                            .toUpperCase()}`,
                        })}
                      </CardAvatar>
                      <CardBody profile>
                        {currency.currencyName === "" ? (
                          <Fragment>
                            <br />
                            <h4 className={classes.cardCategory}> </h4>
                            <br />
                            <br />
                            <br />
                            <br />
                            <br />
                            <br />
                            <br />
                          </Fragment>
                        ) : (
                          <GridContainer>
                            <GridItem xs={12} sm={12} md={6}>
                              <h6 className={classes.cardCategory}></h6>
                              <h4 className={classes.cardTitle}>
                                {currency.currencyName}
                              </h4>
                              <br />
                              <h6 className={classes.cardCategory}></h6>
                              <h4 className={classes.cardTitle}>
                                {" \u20AA" +
                                  currency.value.toFixed(6).slice(0, -4)}
                              </h4>
                            </GridItem>
                            <GridItem xs={12} sm={12} md={6}>
                              <Button
                                onClick={() => handleNoRateInput()}
                                fullWidth
                                color="primary"
                                round
                              >
                                 
                              </Button>
                              <Button
                                onClick={() => handleRateInput()}
                                fullWidth
                                color="primary"
                                round
                              >
                                 
                              </Button>
                              {rateInput && (
                                <TextField
                                  type="number"
                                  autoFocus
                                  label=" "
                                  value={rateValue}
                                  onChange={(e) => setRateValue(e.target.value)}
                                  fullWidth
                                />
                              )}
                              {currency.isInTable && (
                                <Fragment>
                                  <Button
                                    onClick={() =>
                                      handleRateChoise("buyCashRate")
                                    }
                                    fullWidth
                                    color="primary"
                                    round
                                  >
                                     
                                  </Button>
                                  <Button
                                    onClick={() =>
                                      handleRateChoise("sellCashRate")
                                    }
                                    fullWidth
                                    color="primary"
                                    round
                                  >
                                     
                                  </Button>
                                  <Button
                                    onClick={() =>
                                      handleRateChoise("buyTransferRate")
                                    }
                                    fullWidth
                                    color="primary"
                                    round
                                  >
                                     
                                  </Button>
                                  <Button
                                    onClick={() =>
                                      handleRateChoise("sellTransferRate")
                                    }
                                    fullWidth
                                    color="primary"
                                    round
                                  >
                                     
                                  </Button>
                                </Fragment>
                              )}
                            </GridItem>
                          </GridContainer>
                        )}
                      </CardBody>
                    </Card>
                  </GridItem>
                  <GridItem xs={12} sm={12} md={3}>
                    <br />
                    <br />
                    {user.currenciesRates.map(
                      (rate) =>
                        rate.isInTable && (
                          <Button
                            fullWidth
                            color="success"
                            key={rate._id}
                            onClick={() =>
                              handleCurrencyChoise(
                                rate.currencyName,
                                currencies[rate.currencyName],
                                true
                              )
                            }
                          >
                            {rate.currencyName}
                          </Button>
                        )
                    )}
                    <h1>{""}</h1>
                    <br />
                    <Divider />
                    <Button
                      fullWidth
                      color="success"
                      aria-controls="long-menu"
                      aria-haspopup="true"
                      onClick={handleClickProfile}
                    >
                       
                    </Button>
                    <Paper>
                      <Menu
                        id="long-menu"
                        anchorEl={openProfile}
                        open={Boolean(openProfile)}
                        onClose={handleCloseProfile}
                        PaperProps={{
                          style: {
                            maxHeight: 300,
                            width: "35ch",
                          },
                        }}
                      >
                        {Object.keys(currenciesNames).map((curr, i) => (
                          <MenuItem
                            key={i}
                            className={classes.dropdownItem}
                            onClick={() =>
                              handleCurrenciesList(
                                curr,
                                currencies[curr],
                                false
                              )
                            }
                          >
                            <GridContainer>
                              <GridItem>
                                {flag({
                                  size: 24,
                                  code: `${curr.substring(0, 2).toUpperCase()}`,
                                })}
                              </GridItem>
                              <GridItem>
                                <p
                                  className={classes.cardCategory}
                                  style={{
                                    color: "black",
                                    paddingTop: "2px",
                                  }}
                                >
                                  {curr}
                                </p>
                              </GridItem>
                            </GridContainer>
                          </MenuItem>
                        ))}
                      </Menu>
                    </Paper>
                  </GridItem>
                </GridContainer>
              </CardBody>
            </Fragment>
          )}
        </Card>
      ) : (
        <h6 className={classes.cardCategory}>
          <Lottie options={defaultOptions} height={200} width={200}>
            LOADING
          </Lottie>
        </h6>
      )}
    </Fragment>
  );
}

flag.propTypes = {
  code: PropTypes.string.isRequired,
  style: PropTypes.any,
};

const mapDispatchToProps = (dispatch) => ({
  autoLogoutRequest: (email) => dispatch(autoLogoutRequest(email)),
  userLoadRequest: () => dispatch(userLoadRequest()),
  getCurrenciesRequest: () => dispatch(getCurrenciesRequest()),
  resetLoadUser: () => dispatch(resetLoadUser()),
});

const mapStateToProps = (state) => {
  return {
    session: state.userReducer.session,
    lex: state.userReducer.lex,
    loadedUser: state.userReducer.loadedUser,
    user: state.userReducer.user,
    currencies: state.getCurrenciesReducer.currencies,
    loadedCurrencies: state.getCurrenciesReducer.loadedCurrencies,
    autoDisconnect: state.userReducer.autoDisconnect,
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(Calculator));
